import React, { useEffect, useState } from 'react';

const AwsRdsResource = ({ onDatabaseAdd }) => {
  const [instances, setInstances] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');

  useEffect(() => {
    fetchRdsInstances();
  }, []);

  const fetchRdsInstances = async () => {
    setLoading(true);
    setError('');
    try {
      const response = await fetch('/aws/rds-instances', { credentials: 'include' });
      if (response.ok) {
        const data = await response.json();
        setInstances(data.instances || []);
      } else {
        setError('RDS 인스턴스 목록을 불러오지 못했습니다.');
      }
    } catch (e) {
      setError('RDS 인스턴스 조회 중 오류가 발생했습니다.');
    } finally {
      setLoading(false);
    }
  };

  const handleRegisterDb = async (instance) => {
    setSuccess('');
    setError('');
    try {
      // DB 관리에 등록 요청 (예시: /api/databases)
      const formData = new FormData();
      formData.append('name', instance.DBInstanceIdentifier);
      formData.append('host', instance.Endpoint.Address);
      formData.append('port', instance.Endpoint.Port);
      formData.append('user', '');
      formData.append('password', '');
      formData.append('dbname', instance.DBName || '');
      const response = await fetch('/api/databases', {
        method: 'POST',
        body: formData,
        credentials: 'include'
      });
      if (response.ok) {
        setSuccess('DB로 등록되었습니다!');
        if (onDatabaseAdd) onDatabaseAdd();
      } else {
        setError('DB 등록에 실패했습니다.');
      }
    } catch (e) {
      setError('DB 등록 중 오류가 발생했습니다.');
    }
  };

  return (
    <div className="aws-rds-resource">
      <h2>RDS 인스턴스 조회</h2>
      {loading && <div>불러오는 중...</div>}
      {error && <div className="alert alert-error">{error}</div>}
      {success && <div className="alert alert-success">{success}</div>}
      <ul>
        {instances.map((inst) => (
          <li key={inst.DBInstanceIdentifier} className="rds-instance-item">
            <div>
              <strong>{inst.DBInstanceIdentifier}</strong> ({inst.Engine})<br />
              호스트: {inst.Endpoint.Address}:{inst.Endpoint.Port}<br />
              DB명: {inst.DBName || '-'}
            </div>
            <button onClick={() => handleRegisterDb(inst)} className="btn btn-primary btn-sm">DB로 등록</button>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default AwsRdsResource; 